.o-Flex--siteHead {
  margin: 1.5rem 0;
  margin: var(--lh) 0;
  margin: 1lh 0;
  justify-content: flex-start;
  align-items: center;
}
@media not all and (min-width: 45em) {
  .o-Flex--siteHead {
    flex-direction: column;
  }
}
@supports (width: var(--width)) {
  @media screen and (min-width: 45em) {
    header .o-Flex--siteHead {
      margin-right: calc(0.5 * var(--year-badge-width));
    }
  }
}
@media not all and (min-width: 45em) {
  .o-Flex--siteFoot {
    flex-direction: column;
  }
}
@media screen and (min-width: 30em) and (max-width: 44.999em) {
  .o-Flex--siteFoot {
    flex-wrap: wrap;
    max-height: calc((3*5 + 7*2) * var(--lh));
    max-height: 29lh;
  }
}
@media screen and (min-width: 45em) {
  .-d-n-sM {
    display: none;
  }
}
@media not all and (min-width: 45em) {
  .-d-n-nM {
    display: none;
  }
}
.-r-1 {
  right: 1rem !important;
}
.c-FooterIcon,
.-mr-1 {
  margin-right: 1rem;
}
@supports (margin-inline-end: 1rem) {
  .c-FooterIcon,
  .-mr-1 {
    margin-right: unset;
    margin-inline-end: 1rem;
  }
}
.-fw-b {
  font-weight: bold;
}
